import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'municipio-dados',   templateUrl: 'municipio_dados.component.html' }) export class MunicipioDadosComponent implements OnInit {       displayMode: MunicipioDadosDisplayModeEnum;     displayModeEnum = MunicipioDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = MunicipioDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = MunicipioDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = MunicipioDadosDisplayModeEnum.Edit;           break;       }     }  }  enum MunicipioDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


